body {
  /* background-color: #39322c; */
  color: white;
  font-family: Arial, sans-serif;
  font-size: 16px;
}

header, footer {
  background-color: #333;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}

.logo {
  display: flex;
  align-items: center;
}

.logo img {
  width: 50px;
  height: auto;
  margin-right: 1rem;
  }
  
  nav {
  display: flex;
  justify-content: center;
  align-items: center;
  }
  
  nav a {
  color: white;
  text-decoration: none;
  margin: 0 1rem;
  font-weight: bold;
  text-transform: uppercase;
  transition: color 0.3s ease-in-out;
  }
  
  nav a:hover {
  color: #c0c0c0;
  }
  
  section {
  padding: 2rem;
  }
  
  h1 {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 1rem;
  }
  
  h2 {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 1rem;
  }
  
  p {
  margin-bottom: 1rem;
  }
  
  ul {
  margin-bottom: 1rem;
  }
  
  li {
  margin-bottom: 0.5rem;
  }
  
  .btn {
  display: inline-block;
  background-color: #333;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
  }
  
  .btn:hover {
  background-color: #c0c0c0;
  color: black;
  }
  
  .dark-mode-btn {
  position: fixed;
  bottom: 1rem;
  right: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  color: black;
  border: none;
  padding: 0.5rem;
  border-radius: 50%;
  box-shadow: 0px 0px 10px 1px rgba(0, 0, 0, 0.25);
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
  }
  
  .dark-mode-btn:hover {
  background-color: #c0c0c0;
  }
  
  .dark-mode-btn i {
  font-size: 1.5rem;
  }